/* Main container styling */
.app-container {
  display: flex;
  min-height: 100vh;
  background-color: #ffffff;
}

.config-heading {
  font-size: clamp(1.2rem, 4vw, 1.8rem);
  font-weight: bold;
  color: #1e3a8a;
  margin-bottom: 0.5rem;
}

/* Button Container */
.button-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-top: 50px; /* Adjust to position buttons correctly */
}

.toggle-button {
  background-color: #2563eb;
  color: #fff;
  border: none;
  padding: 12px 16px;
  font-size: 24px;
  cursor: pointer;
  border-radius: 0 20px 20px 0; /* Bookmark shape */
  transition: all 0.3s ease;
  position:absolute;
  top: 20px;
  right: -55px; /* Adjust this value to position the button correctly */
  z-index: 1; /* Ensure the button is above other elements */
}

.toggle-button:hover {
  background-color: #3b82f6;
}

/* Sidebar styling */
.sidebar_u {
  width: 250px;
  height: auto;
  background-color: #1c59c2;
  color: white;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  left: 0;
  top: 0px;
  transition: width 0.3s ease-in-out;
}

.sidebar_u.open {
  width: 250px;
}

.sidebar_u.closed {
  width: 0px;
  padding: 0px;
}

.sidebar_u .toggle-button:hover {
  background: #3b82f6;
}

.sidebar_u.closed + .main-content {
  margin-left: 10px;
}

/* Main content styling */
.main-content {
  flex-grow: 1;
  margin-left: 50px;
  margin-right: 50px;
  padding: 2rem;
  transition: margin-left 0.3s ease-in-out;
}

.main-content.sidebar-closed {
  margin-left: 60px;
}

/* Add this CSS for the toggle button */
.toggle-container {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  justify-content: center;
}

.toggle-label {
  display: flex;
  align-items: center;
  cursor: pointer;
  font-size: 1rem;
  color: #fff;
}

.toggle-label input {
  display: none;
}

.toggle-slider {
  width: 50px;
  height: 25px;
  background-color: #ccc;
  border-radius: 20px;
  position: relative;
  margin-left: 10px;
  transition: background-color 0.3s;
}

.toggle-slider::before {
  content: "";
  position: absolute;
  width: 23px;
  height: 23px;
  background-color: #fff;
  border-radius: 50%;
  top: 1px;
  left: 1px;
  transition: transform 0.3s;
}

input:checked + .toggle-slider {
  background-color: #4caf50;
}

input:checked + .toggle-slider::before {
  transform: translateX(25px);
}

/* Upload container styling */
.upload-container {
  max-width: 50rem;
  margin: 0 auto;
  
  background: white;
  border-radius: 20px;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.08);
  padding: 2rem;
}

.upload-sections-wrapper {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
  margin-bottom: 2rem;
  max-height: 30rem;
}

.upload-section {
  max-width: 20rem;
  background: #ffffff;
  border-radius: 16px;
  padding: 1.5rem;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.05);
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.upload-section:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.08);
}

/* Dropzone styling */
.dropzone {
  border: 2px dashed #4785ff;
  background-color: #f1f1f1e5;
  border-radius: 12px;
  padding: 0.5rem;
  text-align: center;
  cursor: pointer;
  transition: all 0.2s ease;
  position: relative;
  overflow: hidden;
}

.dropzone:hover {
  border-color: #2563eb;
  background-color: #f8faff;
}

.dropzone-icon {
  color: #4785ff;
  font-size: 2.5rem;
  margin-bottom: 1rem;
  transition: transform 0.3s ease;
}

.dropzone:hover .dropzone-icon {
  transform: scale(1.1);
}

.dropzone-text {
  font-size: 1rem;
  color: #64748b;
  margin-bottom: 0.5rem;
}

.browse-button {
  display: inline-block;
  background-color: #4785ff;
  color: white;
  padding: 0.5rem 2rem;
  border-radius: 6px;
  font-size: 0.875rem;
  font-weight: 500;
  margin-top: 0.5rem;
  transition: all 0.2s ease;
}

.browse-button:hover {
  background-color: #2563eb;
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(37, 99, 235, 0.2);
}

/* Navigation Buttons */
.nav-button {
  padding: 16px 32px;
  font-size: 1rem;
  font-weight: 600;
  color: #fff;
  background: linear-gradient(135deg, #2563eb, #3b82f6);
  border: none;
  border-radius: 50px;
  cursor: pointer;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
}

/* Hover Effect for Button */
.nav-button:hover {
  transform: translateY(-3px);
  box-shadow: 0 6px 12px rgba(37, 99, 235, 0.2);
}

/* Active State for Button */
.nav-button:active {
  transform: translateY(-1px);
  box-shadow: 0 3px 6px rgba(37, 99, 235, 0.2);
}

/* File list styling */
.file-list {
  margin-top: 1rem;
}

.file-item {
  display: flex;
  align-items: center;
  padding: 0.75rem;
  border-radius: 8px;
  background-color: #f8faff;
  margin-bottom: 0.5rem;
  transition: all 0.2s ease;
}

.file-item:hover {
  background-color: #f1f5ff;
  transform: translateX(2px);
}

.file-icon {
  color: #4785ff;
  margin-right: 0.75rem;
  font-size: 1.25rem;
}

.file-name {
  flex: 1;
  font-size: 0.875rem;
  color: #1e293b;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.remove-file {
  color: #ef4444;
  cursor: pointer;
  padding: 0.25rem;
  border-radius: 50%;
  transition: all 0.2s ease;
}

.remove-file:hover {
  background-color: #fee2e2;
  color: #dc2626;
}

/* Upload button container */
.upload-actions {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 2rem;
}

/* Upload button styling */
.upload-button {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  width: 100%;
  max-width: 300px;
  padding: 1rem 2rem;
  background-color: #1e3a8a;
  color: white;
  border: none;
  border-radius: 12px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
}

.upload-button:hover:not(:disabled) {
  background-color: #0e1013;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(37, 99, 235, 0.2);
}

.upload-button:disabled {
  background-color: #94a3b8;
  cursor: not-allowed;
}

/* Loading animation */
.loading-spinner {
  display: inline-block;
  width: 1.5rem;
  height: 1.5rem;
  border: 3px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  border-top-color: white;
  animation: spin 1s ease-in-out infinite;
  margin-right: 0.5rem;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

/* Upload progress */
.upload-progress {
  width: 100%;
  max-width: 300px;
  margin-top: 1rem;
  text-align: center;
}

.progress-bar {
  width: 100%;
  height: 6px;
  background-color: #e2e8f0;
  border-radius: 3px;
  overflow: hidden;
  margin: 0.5rem 0;
}

.progress-bar-fill {
  height: 100%;
  background-color: #00b12f;
  transition: width 0.3s ease;
}

.upload-message {
  margin-top: 1rem;
  font-size: 0.875rem;
  color: #4785ff;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}

.upload-message.success {
  color: #22c55e;
}

.upload-message.error {
  color: #ef4444;
}

/* Dropdown container styling */
.dropdown-container {
  margin-top: 20px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #f9f9f9;
}

.dropdown-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}

.dropdown-title {
  margin: 0;
  font-size: 1.2rem;
}

.dropdown-title.blue {
  color: blue;
}

.dropdown-title.green {
  color: green;
}

.dropdown-toggle {
  background: none;
  border: none;
  cursor: pointer;
}

.dropdown-container ul {
  list-style-type: none;
  padding: 0;
  margin: 10px 0 0 0;
}

.dropdown-container li {
  margin: 5px 0;
  font-size: 1rem;
  color: #555;
}

.loading-spinner {
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Alert modal styling */
.alert-modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.alert-content {
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  text-align: center;
}

.alert-content p {
  margin-bottom: 20px;
}

.alert-content button {
  background-color: #b12c2c;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.alert-content button:hover {
  transform: scale(1.05);
}

/* Responsive adjustments */
@media (max-width: 1024px) {
  .upload-sections-wrapper {
    grid-template-columns: 1fr;
  }

  .main-content {
    margin-left: 20px;
    margin-right: 20px;
    padding: 1.5rem;
  }

  .upload-container {
    padding: 1.5rem;
  }
}

@media (max-width: 768px) {
  .main-content {
    margin-left: 10px;
    margin-right: 10px;
    padding: 1rem;
  }

  .upload-container {
    padding: 1rem;
  }

  .dropzone {
    padding: 0.3rem;
  }
}