/* General Styling */
body {
  margin: 0;
  font-family: "Poppins", sans-serif;
}

.alert-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.alert-content {
  background: white;
  padding: 20px;
  border-radius: 8px;
  text-align: center;
}

.alert-content p {
  margin-bottom: 20px;
}

.alert-content button {
  padding: 10px 20px;
  background: #1e3a8a;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.alert-content button:hover {
  background: #3b82f6;
}

/* Navbar Container */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 30px;
  background: linear-gradient(90deg, #060829 0%, #1c59c2 100%);
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  position: sticky;
  top: 0;
  z-index: 1000;
}

/* Logo Section */
.navbar-logo .logo-text {
  font-size: 1.8rem;
  font-weight: bold;
  color: #fff;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
  transition: transform 0.3s ease;
}

.navbar-logo .logo-text:hover {
  transform: scale(1.1);
}

/* Navigation Links */
.navbar-links {
  display: flex;
  list-style: none;
  gap: 20px;
  font-weight: bold;
}

.navbar-links li a {
  text-decoration: none;
  color: #ffffff;
  font-size: 1rem;
  position: relative;
  transition: color 0.3s ease;
}

.navbar-links li a::after {
  content: "";
  position: absolute;
  bottom: -5px;
  left: 0;
  width: 100%;
  height: 2px;
  background: #fff;
  transform: scaleX(0);
  transform-origin: right;
  transition: transform 0.3s ease;
}

.navbar-links li a:hover,
.navbar-links li a.active {
  color: #fffa;
}

.navbar-links li a:hover::after,
.navbar-links li a.active::after {
  transform: scaleX(1);
  transform-origin: left;
}


/* Logo Container */
.navbar-logo {
  display: flex;
  align-items: center;
  gap: 10px;
}

.logo-image {
  width: 200px;
  height: auto;
  object-fit: cover; /* Ensures the image is well-fitted */
  transition: transform 0.3s ease;
}

.logo-text {
  font-size: clamp(1.5rem, 3vw, 1.8rem);
  font-weight: bold;
  color: #fff;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

/* Hamburger Menu - Hidden by Default */
.menu-toggle {
  display: none;
  flex-direction: column;
  cursor: pointer;
}

.menu-toggle div {
  width: 30px;
  height: 3px;
  background: #fff;
  margin: 5px;
  transition: all 0.3s ease;
}

/* Mobile Devices Responsive */
@media (max-width: 768px) {
  .navbar {
    padding: 15px 20px;
  }

  .navbar-links {
      display: none;
      flex-direction: column;
      position: absolute;
      top: 60px;
      left: 0;
      width: 100%;
      background: rgba(6, 8, 41, 0.9);
      backdrop-filter: blur(10px);
      text-align: center;
      padding: 20px 0;
  }

  .navbar-links.active {
    display: flex;
  }

  .navbar-links li {
    margin: 10px 0;
  }

  .menu-toggle {
    display: flex;
  }
}
.link-button {
  background: none;
  border: none;
  color: inherit;
  font: inherit;
  cursor: pointer;
  padding: 0;
  text-decoration: none;
}

/* Hamburger Animation */
.menu-toggle.active div:nth-child(1) {
  transform: translateY(8px) rotate(45deg);
}

.menu-toggle.active div:nth-child(2) {
  opacity: 0;
}

.menu-toggle.active div:nth-child(3) {
  transform: translateY(-8px) rotate(-45deg);
}

/* iPad Mini, Air, and Pro (768px - 1024px) */
@media (min-width: 768px) and (max-width: 1024px) {
  .navbar {
    padding: 15px 25px;
  }

  .navbar-links {
    font-size: 1.2rem;
    gap: 15px;
  }

  .navbar-logo img {
    width: 140px;
  }

  .menu-toggle {
    display: none; /* Hide hamburger menu if enough space */
  }
}